import {
  Checkbox,
  FormControl,
  FormControlLabel,
  FormGroup,
  FormHelperText,
  FormLabel,
  Radio,
  RadioGroup,
} from "@material-ui/core";
import React, { FC, memo } from "react";
import { Controller, useFormContext } from "react-hook-form";

interface RadioItems {
  label: string;
  value: string | number;
  disabled?: boolean;
  defaultCheck?: boolean;
}

interface MultCheckboxHookFormProps {
  //extends Partial<> {
  name: string;
  label: string;
  variant?: "standard" | "outlined" | "filled";
  row?: boolean;
  radio_items: Array<RadioItems>;
}

export const MultCheckboxHookForm: FC<MultCheckboxHookFormProps> = memo(
  (props) => {
    const { control, errors, setValue, getValues } = useFormContext();

    let error = false;
    let error_message = "";

    if (errors && errors?.hasOwnProperty(props?.name)) {
      error = true;
      error_message = errors[props?.name]?.message;
    }

    console.log(`error`, error, error_message);

    return (
      <FormControl error={error} variant={props.variant} component="fieldset">
        <FormLabel component="legend">{props.label}</FormLabel>
        <FormGroup row={props.row}>
          {props.radio_items.map((radio, index) => {
            return (
              <Controller
                key={props.name + `[${index}]`}
                name={props.name + `[${index}]`}
                control={control}
                render={(ctrlProps, { invalid, isTouched, isDirty }) => {
                  return (
                    <FormControlLabel
                      onChange={(e: any) => {
                        setValue(
                          props.name + `[${index}]`,
                          ctrlProps.value ? undefined : e.target.value,
                          {
                            shouldDirty: true,
                            shouldValidate: true,
                          }
                        );

                        const values = getValues()[props.name].filter(
                          (v) => v !== null || v !== "undefined" || v !== ""
                        );

                        console.log(`values`, values);

                        setValue(props.name, values);

                        // setValue(props.name,)
                      }}
                      name={ctrlProps.name}
                      value={radio.value}
                      control={<Checkbox />}
                      label={radio.label}
                      defaultChecked={radio.defaultCheck}
                    />
                  );
                }}
              />
            );
          })}
        </FormGroup>
        <FormHelperText>{error_message}</FormHelperText>
      </FormControl>
    );
  }
);

export default MultCheckboxHookForm;
