import { yupResolver } from "@hookform/resolvers/yup";
import { Button, Chip, Grid } from "@material-ui/core";
import React, { FC, memo, useCallback, useState } from "react";
import { FormProvider, useForm } from "react-hook-form";
import styled from "styled-components";
import * as yup from "yup";
import FormDialog from "../../../Component/FormDialog/FormDialog";
import DateFieldHookForm from "../../../Component/HookForm/DateFieldHookForm";
import MultCheckboxHookForm from "../../../Component/HookForm/MultCheckboxHookForm";
import TextFieldHookForm from "../../../Component/HookForm/TextFieldHookForm";
import ITableInitialSort from "../../../Services/Interface/ITableInitialSort";
import { PaginationModel } from "../../../Services/Models/PaginationModels";
interface IFilterDtNewsAdminView {}

const initialTableSort: Array<ITableInitialSort> = [
  {
    label: "Pinakauna mahitabo",
    value: {
      column: "sched_at",
      direction: "desc",
    },
  },
  {
    label: "Pinakaulahi mahitabo",
    value: {
      column: "sched_at",
      direction: "asc",
    },
  },
  {
    label: "Titulo pasaka",
    value: {
      column: "title",
      direction: "asc",
    },
  },
  {
    label: "Titulo pasaka",
    value: {
      column: "title",
      direction: "desc",
    },
  },
];

// const schema: any = yup.object({
//   sts_pk: yup.array().of(yup.string().nullable()).ensure().cast("ds"),
// });

const schema = yup.object().shape({
  sts_pk: yup.array(yup.string()).min(1).compact(),
});

export const FilterDtNewsAdminView: FC<IFilterDtNewsAdminView> = memo(() => {
  const [open_dialog, set_open_dialog] = useState(false);

  const form_add_news = useForm({
    mode: "onBlur",
    resolver: yupResolver(schema),
  });

  const [selected_sort, set_selected_sort] = useState(0);

  const handleChangeSort = useCallback((index: number) => {
    set_selected_sort(index);
  }, []);

  const handleSubmitForm = useCallback(
    (data) => {
      console.log(`data`, data);
      const payload: PaginationModel = {
        filters: {
          search: data.search,
        },
        sort: initialTableSort[selected_sort].value,
        page: null,
      };

      console.log(`payload`, payload);
    },
    [selected_sort]
  );

  return (
    <>
      <Button
        variant="contained"
        color="primary"
        onClick={() => set_open_dialog(true)}
      >
        Pagsala
      </Button>
      <FormDialog
        title="Isala ang resulta sa mga balita"
        handleClose={() => set_open_dialog(false)}
        open={open_dialog}
        minWidth={600}
        body={
          <StyledFilter>
            <FormProvider {...form_add_news}>
              <form
                onSubmit={form_add_news.handleSubmit(handleSubmitForm)}
                noValidate
                id="form-filter-records"
              >
                <Grid container justify="center" spacing={2}>
                  <Grid item xs={12}>
                    <div className="filter-main-title">Pagpangita</div>
                    <TextFieldHookForm
                      name="search"
                      variant="outlined"
                      fullWidth={true}
                      placeholder="Ibutang ang mga letra sa titulo nga gusto pangitaon"
                      InputLabelProps={{
                        shrink: true,
                      }}
                    />
                  </Grid>

                  <Grid item xs={12}>
                    <div className="filter-main-title">Matang</div>
                    <Grid container spacing={1}>
                      {initialTableSort.map((s, i) => (
                        <Grid key={i} item>
                          <Chip
                            label={s.label}
                            clickable
                            color={selected_sort === i ? "primary" : "default"}
                            onClick={() => {
                              handleChangeSort(i);
                            }}
                          />
                        </Grid>
                      ))}
                    </Grid>
                  </Grid>
                  <Grid item xs={12}>
                    <div className="filter-main-title">Pagsala</div>
                    <Grid container spacing={2}>
                      <Grid item xs={12}>
                        <MultCheckboxHookForm
                          label="Kahimtang"
                          name="sts_pk"
                          row={true}
                          radio_items={[
                            {
                              label: "Unpublished",
                              value: "u",
                            },
                            {
                              label: "Published",
                              value: "p",
                            },
                          ]}
                        />
                      </Grid>
                      <Grid item xs={6}>
                        <DateFieldHookForm
                          type="date"
                          name="date_from"
                          label="Mahitabo Gikan"
                          InputLabelProps={{
                            shrink: true,
                          }}
                          clearable
                          fullWidth
                          inputVariant="outlined"
                          autoOk
                          defaultValue={null}
                        />
                      </Grid>
                      <Grid item xs={6}>
                        <DateFieldHookForm
                          type="date"
                          name="date_to"
                          label="Mahitabo Hangtod"
                          InputLabelProps={{
                            shrink: true,
                          }}
                          clearable
                          fullWidth
                          inputVariant="outlined"
                          autoOk
                          defaultValue={null}
                        />
                      </Grid>
                    </Grid>
                  </Grid>
                </Grid>
              </form>
            </FormProvider>
          </StyledFilter>
        }
        actions={
          <>
            <Button type="reset" onClick={() => {}} variant="contained">
              Reset
            </Button>

            <Button
              variant="contained"
              form="form-filter-records"
              color="primary"
              type="submit"
            >
              Apply Filters
            </Button>
          </>
        }
      />
    </>
  );
});

export default FilterDtNewsAdminView;

const StyledFilter = styled.div`
  /* text-align: center; */

  .filter-main-title {
    padding: 0.2em 0;
    /* border-bottom: 0.1em solid rgba(0, 0, 0, 0.15); */
    font-weight: 900;
    margin: 0.5em 0;
    opacity: 0.37;
    text-align: start;
  }
`;
